import React from 'react';
import { Link } from 'react-router-dom';
import { Form, Button } from "react-bootstrap"

export default ({ passwordChange, usernameChange, handleSubmit, username, password, tipo }) => (
    <div className="fondo">
        <div className="container peliculas">
            <div className="row">
                <div className="col-6 centrado">
                    <h4 id="login" className="loginh4">REGISTRARSE</h4>
                    <Form onSubmit={handleSubmit}>
                        <Form.Group controlId="formBasicEmail">
                            <Form.Label>Email</Form.Label>
                            <Form.Control 
                            onChange={usernameChange} 
                            name="username" 
                            type="email" 
                            placeholder="Ingrese su Email"
                            value={username}
                            />
                        </Form.Group>
                        <Form.Group controlId="formBasicPassword">
                            <Form.Label>Contrasea</Form.Label>
                            <Form.Control 
                            type="password" 
                            placeholder="Contrasea"
                            onChange={passwordChange}
                            name="password" 
                            value={password}
                            />
                        </Form.Group>
                        <Button className="submitButton" type="submit">
                            Registrarse
                        </Button>
                    </Form>
                </div>
                <div className="col-6 beneficios">
                      <h4 className="loginh4 centrado">CREAR UNA CUENTA</h4>     
                      <p className="bajar" id="minuscula">Crear una cuenta es fcil. Introduce tu direccin de correo electrnico, diligencia el formulario de la pgina siguiente y disfruta de los beneficios de tener una cuenta.</p>
                      <ul>
                          <li>
                          Una sola cuenta global con la que podrs acceder a todos los productos y servicios de adidas.
                          </li>
                          <li>
                          Finalizar tus pedidos ms rpido

                          </li>
                          <li>
                          Consultar tu historial de pedidos

                          </li>
                      </ul>
                      {tipo=="Registrarse" ? (<></>):(<>
                      <Link to="/user/register"><Button className="submitButton" id="registrase">Registrarse</Button></Link>
                      </>)}
                  </div>
            </div>
        </div>
    </div>
);
